<template>
  <div class="carousel">
    <fx-card title="基础用法">
      <fx-carousel height="300">
        <fx-carousel-item v-for="item in picList" :key="item.name">
          <img :src="item.src" :alt="item.name" class="pic">
        </fx-carousel-item>
      </fx-carousel>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="控制器">
      <fx-radio-group v-model="arrowCheck" class="mb10">
        <fx-radio-button label="never">never</fx-radio-button>
        <fx-radio-button label="always">always</fx-radio-button>
        <fx-radio-button label="hover">hover</fx-radio-button>
      </fx-radio-group>
      <fx-carousel height="300" :arrow="arrowCheck" class="mb20">
        <fx-carousel-item v-for="item in picList" :key="item.name">
          <img :src="item.src" alt="路飞" class="pic">
        </fx-carousel-item>
      </fx-carousel>
      <template #hide>
        <pre v-highlight="arrow"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="自定义控制">
      <fx-carousel height="300" arrow="hover" class="mb20" :auto-play="autoPlay" :interval="interval">
        <fx-carousel-item v-for="item in picList" :key="item.name">
          <img :src="item.src" alt="路飞" class="pic">
        </fx-carousel-item>
      </fx-carousel>
      <fx-button @click="autoPlay=!autoPlay" class="mr10">{{ autoPlay ? '关闭自动播放' : '开启自动播放' }}</fx-button>
      <fx-input-number v-model="interval" :min="300"></fx-input-number>
      <template #hide>
        <pre v-highlight="auto"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref } from 'vue'

export default defineComponent({
  name: 'viewCarousel',
  setup () {
    const index = ref<number>(0)
    const arrowCheck = ref<string>('never')
    const autoPlay = ref<boolean>(true)
    const interval = ref<number>(1000)
    const picList = reactive([
      { src: require('../../assets/1.jpg'), name: '路飞' },
      { src: require('../../assets/2.jpg'), name: '索隆' },
      { src: require('../../assets/3.jpg'), name: '娜美' },
      { src: require('../../assets/4.jpg'), name: '乌索普' },
      { src: require('../../assets/5.jpg'), name: '山治' },
      { src: require('../../assets/6.jpg'), name: '乔巴' },
      { src: require('../../assets/7.jpg'), name: '罗宾' },
      { src: require('../../assets/8.jpg'), name: '佛兰奇' },
      { src: require('../../assets/9.jpg'), name: '布鲁克' },
      { src: require('../../assets/10.jpg'), name: '甚平' }
    ])

    const base = `<fx-carousel height="300">
  <fx-carousel-item v-for="item in picList" :key="item.name">
    <img :src="item.src" :alt="item.name" class="pic">
  </fx-carousel-item>
</fx-carousel>`

    const arrow = `<fx-radio-group v-model="arrowCheck" class="mb10">
  <fx-radio-button label="never">never</fx-radio-button>
  <fx-radio-button label="always">always</fx-radio-button>
  <fx-radio-button label="hover">hover</fx-radio-button>
</fx-radio-group>
<fx-carousel height="300" :arrow="arrowCheck" class="mb20">
  <fx-carousel-item v-for="item in picList" :key="item.name">
    <img :src="item.src" alt="路飞" class="pic">
  </fx-carousel-item>
</fx-carousel>`

    const auto = `<fx-carousel height="300" arrow="hover" class="mb20" :auto-play="autoPlay" :interval="interval">
  <fx-carousel-item v-for="item in picList" :key="item.name">
    <img :src="item.src" alt="路飞" class="pic">
  </fx-carousel-item>
</fx-carousel>
<fx-button @click="autoPlay=!autoPlay" class="mr10">{{ autoPlay ? '关闭自动播放' : '开启自动播放' }}</fx-button>
<fx-input-number v-model="interval" :min="300"></fx-input-number>`

    return {
      index,
      arrowCheck,
      autoPlay,
      interval,
      picList,
      base,
      arrow,
      auto
    }
  }
})
</script>

<style scoped lang="scss">
.carousel {
  .pic {
    width: 100%;
    height: 100%;
    object-fit: fill;
  }

  span {
    display: inline-block;
    width: 100%;
    height: 100%;
    color: #fff;
  }
}
</style>
